html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  white-space: nowrap;
  border: 0; }

body {
  font-family: 'Poppins', Arial, Helvetica, sans-serif;
  font-weight: 400;
  position: relative;
  min-width: 320px;
  margin: 0;
  font-size: 15px;
  line-height: 20px;
  color: #fff; }

ul,
li,
p {
  padding: 0;
  margin: 0;
  list-style: none; }

a {
  color: inherit;
  text-decoration: none; }

img,
picture {
  display: block;
  width: 100%;
  height: auto; }

.container {
  width: auto;
  min-width: 320px;
  padding: 0 15px; }
  @media (min-width: 1200px) {
    .container {
      max-width: 1200px;
      padding: 0 15px;
      margin: 0 auto; } }

input[type='number'] {
  -moz-appearance: textfield; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none; }

.header {
  width: 100%;
  min-height: 120px;
  background-image: linear-gradient(0deg, rgba(7, 29, 104, 0.1) 0, rgba(11, 75, 236, 0.1) 100%); }
  .header__container {
    display: flex;
    min-height: 120px; }
  .header__navigation {
    margin-left: auto; }

.logo {
  display: flex;
  align-items: center; }
  .logo__img {
    width: 40px;
    height: 43px;
    margin-right: 15px; }
  .logo__slogan {
    font-family: 'Poppins', Arial, Helvetica, sans-serif;
    font-weight: 300;
    font-size: 12px;
    line-height: 14px; }
  .logo__highlight {
    font-family: 'Poppins', Arial, Helvetica, sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 26px;
    text-shadow: 0 3px 3px rgba(30, 28, 28, 0.75); }

.navigation {
  font-family: 'Poppins', Arial, Helvetica, sans-serif;
  font-weight: 600;
  display: flex;
  font-size: 14px;
  line-height: 1.5; }
  .navigation__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    text-transform: uppercase; }
  .navigation__item:not(:last-child) {
    margin-right: 40px; }
  .navigation__user-list {
    margin-left: 40px; }

.user-list {
  display: flex;
  align-items: center; }
  .user-list__link {
    padding: 10px 15px;
    color: #0b4bec;
    text-transform: capitalize;
    background-color: #fff;
    border-radius: 12px; }

.main-section {
  min-height: calc(100vh - 120px); }
  .main-section__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; }
  .main-section__title {
    font-family: 'Poppins', Arial, Helvetica, sans-serif;
    font-weight: 600;
    font-size: 40px;
    line-height: 1.5; }
  .main-section__text {
    font-family: 'Nunito', Arial, Helvetica, sans-serif;
    font-weight: 400;
    max-width: 945px;
    font-size: 14px;
    line-height: 1.5;
    text-align: center; }
  .main-section__button {
    font-family: 'Poppins', Arial, Helvetica, sans-serif;
    font-weight: 600;
    padding: 15px 45px;
    margin: 56px 0 90px;
    color: #0b4bec;
    text-transform: uppercase;
    cursor: pointer;
    background-color: #fff;
    border: none;
    border-radius: 22px; }
  .main-section__picture {
    position: relative;
    max-width: 570px; }
    .main-section__picture::after {
      position: absolute;
      bottom: -145px;
      left: 0;
      width: 100%;
      height: 145px;
      content: "";
      background-image: url("../img/img/card@1x.png");
      background-position: bottom;
      opacity: 0.2;
      transform: scale(1, -1); }

.page {
  background: linear-gradient(342deg, #071d68 0%, #0b4bec 100%); }

.get-card {
