.message {
  height: 1px;
  min-height: 580px;
  color: #000;
  background-color: #fff; }
  .message__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%; }
  .message__title {
    font-family: 'Poppins', Arial, Helvetica, sans-serif;
    font-weight: 600;
    margin: 0 0 50px;
    font-size: 30px;
    line-height: 1.5; }
  .message__form {
    font-family: 'Raleway', Arial, Helvetica, sans-serif;
    font-weight: 400;
    display: grid;
    grid-template-rows: repeat(3, 1fr);
    grid-template-columns: 1fr 1fr;
    row-gap: 30px;
    column-gap: 70px;
    font-size: 14px;
    line-height: 1.5;
    grid-template-areas: "name message" "email message" "subject button"; }
  .message__input {
    padding: 16px 20px;
    border: 1px solid rgba(0, 0, 0, 0.2); }
    .message__input--name {
      grid-area: name; }
    .message__input--email {
      grid-area: email; }
    .message__input--subject {
      grid-area: subject; }
  .message__textarea {
    grid-area: message;
    padding: 20px;
    resize: none;
    border: 1px solid rgba(0, 0, 0, 0.2); }
  .message__button {
    font-family: 'Raleway', Arial, Helvetica, sans-serif;
    font-weight: 400;
    grid-area: button;
    font-size: 14px;
    line-height: 1.5;
    color: #fff;
    cursor: pointer;
    background-color: #0b4bec;
    border: none; }
